.uiux-designer {
    text-transform: uppercase;
  }
  .text,
  .title,
  .uiux-designer {
    align-self: stretch;
    position: relative;
  }
  .title {
    margin: 0;
    font-size: inherit;
    line-height: 120%;
    font-weight: 700;
    font-family: inherit;
    white-space: pre-wrap;
  }
  .text {
    line-height: 150%;
  }
  .subheadline {
    align-self: stretch;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    font-size: var(--font-size-5xl);
    color: var(--font-medium-emphasis);
    font-family: var(--font-nunito);
  }
  .text1 {
    position: relative;
    font-size: var(--body-18-size);
    line-height: 150%;
    font-weight: 500;
    font-family: var(--font-roboto);
    color: var(--font-high-emphasis);
    text-align: left;
    display: inline-block;
    min-width: 67px;
  }
  .button-primary {
    cursor: pointer;
    border: 0;
    padding: var(--padding-5xs) var(--padding-5xl);
    background-color: var(--brand-yellow);
    border-radius: var(--br-5xs);
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
  }
  .button-primary:hover {
    background-color: var(--color-darkgoldenrod);
  }
  .text2 {
    position: relative;
    font-size: var(--body-18-size);
    line-height: 150%;
    font-weight: 500;
    font-family: var(--font-roboto);
    color: var(--font-high-emphasis);
    text-align: left;
    display: inline-block;
    min-width: 69px;
  }
  .button-secondary,
  .buttons {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
  }
  .button-secondary {
    cursor: pointer;
    border: 2px solid var(--font-high-emphasis);
    padding: var(--padding-8xs) var(--padding-3xl);
    background-color: transparent;
    flex: 1;
    border-radius: var(--br-5xs);
  }
  .button-secondary:hover {
    background-color: var(--color-dimgray-200);
    border: 2px solid var(--color-dimgray-100);
    box-sizing: border-box;
  }
  .buttons {
    width: 264px;
    padding: 0 var(--padding-xl) 0 0;
    box-sizing: border-box;
    gap: var(--gap-xs);
  }
  .content {
    align-self: stretch;
    gap: var(--gap-13xl);
    font-size: var(--title-h1-size);
    color: var(--font-high-emphasis);
    font-family: var(--title-h2);
  }
  .content,
  .intro,
  .intro-wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
  }
  .intro {
    align-self: stretch;
    overflow: hidden;
    gap: var(--gap-xs);
  }
  .intro-wrapper {
    width: 486px;
    padding: var(--padding-91xl) 0 0;
    box-sizing: border-box;
    min-width: 486px;
    max-width: 100%;
  }
  .image-icon {
    height: 616px;
    flex: 1;
    position: relative;
    max-width: 43%;
    overflow: hidden;
    /* object-fit: cover; */
    min-width: 468px;
    margin-top: 70px;
    margin-left: 10px;
    border-radius: 50px;
  }
  .frame-parent {
    align-self: stretch;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 180px;
    max-width: 100%;
    text-align: left;
    font-size: var(--font-size-xl);
    color: var(--brand-yellow);
    font-family: var(--font-nunito);
  }
  @media screen and (max-width: 1200px) {
    .intro-wrapper {
      flex: 1;
    }
    .frame-parent {
      flex-wrap: wrap;
    }
  }
  @media screen and (max-width: 1050px) {
    .title {
      font-size: var(--font-size-32xl);
      line-height: 61px;
    }
  }
  @media screen and (max-width: 750px) {
    .content {
      gap: var(--gap-base);
    }
    .intro-wrapper {
      padding-top: var(--padding-52xl);
      box-sizing: border-box;
      min-width: 100%;
    }
    .image-icon {
      min-width: 90%;
    }
    .frame-parent {
      gap: var(--gap-38xl);
    }
  }
  @media screen and (max-width: 450px) {
    .uiux-designer {
      font-size: var(--font-size-base);
    }
    .title {
      font-size: var(--font-size-19xl);
      line-height: 46px;
    }
    .text {
      font-size: var(--font-size-lgi);
      line-height: 29px;
    }
    .frame-parent {
      gap: var(--gap-9xl);
    }
  }